@import '../../styles/variables';

.admin-embedding-page {
  height: 100vh;
  height: 100dvh; /* dynamic viewport for mobile */
  display: flex;
  flex-direction: column;
  overflow: hidden;
  background: $gray-100;
  padding: 24px;
  box-sizing: border-box;
}

.admin-embedding-page__header {
  margin-bottom: 12px;
  flex-shrink: 0;
}

.admin-embedding-page__title {
  font-size: 1.5rem;
  font-weight: 700;
  color: $gray-900;
  margin: 0;
}

.admin-embedding-page__error {
  padding: 12px 16px;
  background: rgba($error, 0.1);
  color: $error;
  border-radius: 8px;
  margin-bottom: 16px;
}

.admin-embedding-page__layout {
  display: flex;
  gap: 16px;
  max-width: 1400px;
  flex: 1;
  overflow: hidden;
}

.admin-embedding-page__sidebar {
  width: 280px;
  flex-shrink: 0;
  overflow-y: auto;
  background: $white;
  border-radius: 12px;
  padding: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.admin-embedding-page__sidebar-title {
  font-size: 0.875rem;
  font-weight: 700;
  color: $gray-800;
  margin: 0 0 16px 0;
  padding-bottom: 8px;
  border-bottom: 1px solid $gray-200;

  &--pending {
    color: $error;
  }
}

.admin-embedding-page__pending-section {
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid $gray-200;
}

.admin-embedding-page__pending-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.admin-embedding-page__pending-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  padding: 8px 10px;
  background: rgba($error, 0.06);
  border-radius: 8px;
  border: 1px solid rgba($error, 0.2);
}

.admin-embedding-page__pending-info {
  display: flex;
  flex-direction: column;
  gap: 2px;
  font-size: 0.75rem;
  color: $gray-700;

  strong {
    color: $gray-800;
    font-size: 0.8125rem;
  }
}

.admin-embedding-page__register-btn {
  padding: 4px 10px;
  font-size: 0.75rem;
  font-weight: 600;
  background: $white;
  color: $error;
  border: 1px solid $error;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap;

  &:hover {
    background: $error;
    color: $white;
  }
}

.admin-embedding-page__partner-list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.admin-embedding-page__partner-group {
  margin-bottom: 8px;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid transparent;
}

.admin-embedding-page__partner-header {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 12px;
  background: $gray-50;
  transition: background 0.2s;

  &:hover {
    background: $gray-100;
  }
}

.admin-embedding-page__partner-name {
  font-size: 0.8125rem;
  font-weight: 700;
  color: $brand-brown;
  margin: 0;
  flex: 1;
}

.admin-embedding-page__add-hospital-btn {
  width: 24px;
  height: 24px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  background: $brand-brown;
  color: $white;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background: $brand-brown-hover;
  }
}

.admin-embedding-page__hospital-list {
  list-style: none;
  padding: 4px 8px 8px 8px;
  margin: 0;
  background: $white;
}

.admin-embedding-page__hospital-btn {
  display: block;
  width: 100%;
  text-align: left;
  padding: 10px 12px;
  border: none;
  border-radius: 8px;
  background: transparent;
  color: $gray-800;
  font-size: 0.875rem;
  cursor: pointer;
  margin-bottom: 4px;

  &:hover {
    background: $gray-100;
  }

  &.is-selected {
    background: $brand-brown-light;
    color: $brand-brown-darker;
    font-weight: 600;
  }
}

.admin-embedding-page__hospital-name {
  display: block;
}

.admin-embedding-page__hospital-meta {
  font-size: 0.75rem;
  color: $gray-500;
  margin-top: 2px;
}

.admin-embedding-page__main {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0;
  min-width: 0;
  overflow: hidden;
}

.admin-embedding-page__card {
  background: $white;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.admin-embedding-page__card-title {
  font-size: 1rem;
  font-weight: 700;
  color: $gray-800;
  margin: 0 0 8px 0;
}

.admin-embedding-page__actions {
  display: flex;
  gap: 12px;
  margin-top: 16px;
}

.admin-embedding-page__upload-btn,
.admin-embedding-page__rebuild-btn {
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 0.875rem;
  cursor: pointer;
  border: 1px solid $gray-300;

  input {
    display: none;
  }
}

.admin-embedding-page__upload-btn {
  background: $white;
  color: $gray-700;

  &:hover:not(:has(input:disabled)) {
    background: $gray-50;
  }
}

.admin-embedding-page__rebuild-btn {
  background: $brand-brown;
  color: $white;
  border-color: $brand-brown;

  &:hover:not(:disabled) {
    background: $brand-brown-hover;
  }

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
}

.admin-embedding-page__doc-list {
  list-style: none;
  padding: 0;
  margin: 0;

  li {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    border-bottom: 1px solid $gray-200;
    font-size: 0.875rem;
  }
}

.admin-embedding-page__muted {
  color: $gray-500;
  font-size: 0.875rem;
  margin: 0;
}

.admin-embedding-page__config-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 16px;
}

.admin-embedding-page__form-row {
  display: flex;
  gap: 16px;

  .admin-embedding-page__form-group {
    flex: 1;
  }
}

.admin-embedding-page__form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;

  label {
    font-size: 0.875rem;
    font-weight: 600;
    color: $gray-700;
  }

  select, input, textarea {
    padding: 10px 12px;
    border: 1px solid $gray-300;
    border-radius: 8px;
    font-size: 0.875rem;
    background: $white;

    &:focus {
      outline: none;
      border-color: $brand-brown;
      box-shadow: 0 0 0 2px rgba($brand-brown, 0.1);
    }
  }

  textarea {
    resize: vertical;
  }
}

.admin-embedding-page__save-btn {
  align-self: flex-start;
  padding: 10px 24px;
  background: $brand-brown;
  color: $white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  margin-top: 8px;

  &:hover:not(:disabled) {
    background: $brand-brown-hover;
  }

  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
}

.admin-embedding-page__modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.admin-embedding-page__modal-content {
  background: $white;
  border-radius: 12px;
  padding: 24px;
  width: 400px;
  max-width: 90vw;

  h3 {
    margin: 0 0 16px 0;
    font-size: 1.125rem;
    font-weight: 700;
    color: $gray-800;
  }
}

.admin-embedding-page__modal-actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  margin-top: 20px;

  button {
    padding: 8px 16px;
    border-radius: 8px;
    font-size: 0.875rem;
    cursor: pointer;
    border: 1px solid $gray-300;

    &:first-child {
      background: $white;
      color: $gray-700;
      &:hover {
        background: $gray-50;
      }
    }

    &:last-child {
      background: $brand-brown;
      color: $white;
      border-color: $brand-brown;
      &:hover:not(:disabled) {
        background: $brand-brown-hover;
      }
      &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
      }
    }
  }
}

.admin-embedding-page__empty-state {
  text-align: center;
  padding: 60px 20px;
  color: $gray-500;

  h3 {
    font-size: 1.25rem;
    font-weight: 600;
    color: $gray-700;
    margin: 0 0 8px 0;
  }
}

.admin-embedding-page__tabs {
  display: flex;
  gap: 8px;
  margin-top: 16px;

  button {
    padding: 8px 16px;
    border: none;
    background: $gray-100;
    color: $gray-600;
    border-radius: 8px;
    font-size: 0.875rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      background: $gray-200;
    }

    &.active {
      background: $brand-brown;
      color: $white;
    }
  }
}

.admin-embedding-page__chat-container {
  display: flex;
  gap: 16px;
  flex: 1;
  overflow: hidden;
}

.admin-embedding-page__chat-list-card {
  flex: 1.2;
  min-width: 320px;
  background: $white;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
}

.admin-embedding-page__chat-detail-card {
  flex: 1.3;
  min-width: 0;
  background: $white;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
}

.admin-embedding-page__table-wrapper {
  overflow-y: auto;
  flex: 1;
}

.admin-embedding-page__table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.875rem;

  th {
    text-align: left;
    padding: 12px;
    border-bottom: 2px solid $gray-200;
    color: $gray-600;
    font-weight: 600;
  }

  td {
    padding: 12px;
    border-bottom: 1px solid $gray-100;
    cursor: pointer;
  }

  tr:hover td {
    background: $gray-50;
  }

  tr.is-selected td {
    background: $brand-brown-light;
    color: $brand-brown-darker;
    font-weight: 600;
  }
}

.admin-embedding-page__view-btn {
  padding: 4px 8px;
  font-size: 0.75rem;
  border: 1px solid $gray-300;
  background: $white;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background: $gray-50;
  }
}

.admin-embedding-page__chat-window {
  flex: 1;
  background: #fdfaf6;
  border-radius: 12px;
  border: 1px solid $gray-200;
  overflow-y: auto;
  padding: 16px;
}

.admin-embedding-page__chat-messages {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.admin-embedding-page__chat-msg {
  display: flex;
  flex-direction: column;
  max-width: 85%;

  &.user {
    align-self: flex-end;
    .admin-embedding-page__chat-bubble {
      background: $brand-brown;
      color: $white;
      border-bottom-right-radius: 2px;
    }
    .admin-embedding-page__chat-time {
      text-align: right;
    }
  }

  &.assistant {
    align-self: flex-start;
    .admin-embedding-page__chat-bubble {
      background: #f1e6db;
      color: $gray-800;
      border-bottom-left-radius: 2px;
    }
  }
}

.admin-embedding-page__chat-bubble {
  padding: 10px 14px;
  border-radius: 12px;
  font-size: 0.875rem;
  line-height: 1.5;
  white-space: pre-wrap;
  word-break: break-all;
}

.admin-embedding-page__chat-time {
  font-size: 0.7rem;
  color: $gray-400;
  margin-top: 4px;
}

.admin-embedding-page__empty-chat {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $gray-400;
  font-style: italic;
}

/* Connected tabs & fixed heights */
.admin-embedding-page__main > .admin-embedding-page__card:first-child {
  border-radius: 12px 12px 0 0;
}

.admin-embedding-page__main > .admin-embedding-page__card + .admin-embedding-page__card {
  border-radius: 0 0 12px 12px;
  border-top: 1px solid $gray-200;
  flex: 1;
  overflow-y: auto;
}

.admin-embedding-page__main > .admin-embedding-page__card + .admin-embedding-page__chat-container {
  border-radius: 0 0 12px 12px;
  border-top: 1px solid $gray-200;
  background: $white;
  padding: 12px 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.admin-embedding-page__chat-list-card,
.admin-embedding-page__chat-detail-card {
  overflow-y: auto;
}

.admin-embedding-page__error {
  flex-shrink: 0;
}

/* === 공통 문서 섹션 === */
.admin-embedding-page__common-docs-section {
  margin-bottom: 24px;
  padding: 16px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;

  h4 {
    margin: 0 0 12px;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 8px;
  }
}

.admin-embedding-page__doc-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 20px;
  height: 20px;
  padding: 0 6px;
  border-radius: 10px;
  background: #4263eb;
  color: #fff;
  font-size: 11px;
  font-weight: 600;
}

.admin-embedding-page__doc-list-actions {
  display: flex;
  align-items: center;
  gap: 8px;
}

.admin-embedding-page__doc-delete-btn {
  padding: 2px 8px;
  font-size: 0.75rem;
  background: #fff;
  color: #e03131;
  border: 1px solid #e03131;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap;
  &:hover {
    background: #e03131;
    color: #fff;
  }
}


/* === RAG 테스트 패널 (embed 모드) === */
.admin-embedding-page__rag-panel {
  width: 360px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  overflow: hidden;
}

.admin-embedding-page__rag-panel-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 16px;
  border-bottom: 1px solid #e9ecef;
  flex-shrink: 0;

  h3 {
    margin: 0;
    font-size: 15px;
    font-weight: 700;
    color: #212529;
  }
}

.admin-embedding-page__rag-panel-empty {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #adb5bd;
  font-size: 13px;
  padding: 40px 16px;
  text-align: center;
}

.admin-embedding-page__rag-chat-messages {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 12px;
  overflow-y: auto;
  flex: 1;
  min-height: 0;
}

.admin-embedding-page__chat-bubble--user,
.admin-embedding-page__chat-bubble--assistant {
  max-width: 85%;
  padding: 10px 14px;
  border-radius: 12px;
  font-size: 13px;
  line-height: 1.6;
  word-break: break-word;
  white-space: pre-wrap;
}

.admin-embedding-page__chat-bubble--user {
  align-self: flex-end;
  background: $brand-brown;
  color: #fff;
  border-bottom-right-radius: 4px;
}

.admin-embedding-page__chat-bubble--assistant {
  align-self: flex-start;
  background: #f8f5f0;
  color: #212529;
  border-bottom-left-radius: 4px;
}

.admin-embedding-page__rag-panel-body {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
  min-height: 0;
}

.admin-embedding-page__rag-panel-input {
  display: flex;
  gap: 8px;
  padding: 12px;
  border-top: 1px solid #e9ecef;
  flex-shrink: 0;

  input {
    flex: 1;
    padding: 10px 12px;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    font-size: 13px;
    outline: none;
    &:focus { border-color: $brand-brown; box-shadow: 0 0 0 2px rgba($brand-brown, 0.1); }
    &:disabled { background: #f8f9fa; }
  }

  button {
    padding: 10px 16px;
    background: $brand-brown;
    color: #fff;
    border: none;
    border-radius: 8px;
    font-size: 13px;
    font-weight: 600;
    cursor: pointer;
    white-space: nowrap;
    &:hover { background: $brand-brown-hover; }
    &:disabled { background: #adb5bd; cursor: not-allowed; }
  }
}

.admin-embedding-page__rag-panel-header button {
  padding: 4px 10px;
  font-size: 12px;
  background: #f1f3f5;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  cursor: pointer;
  &:hover { background: #e9ecef; }
}

/* embed 모드 레이아웃 안정화 */
.admin-embedding-page--embed {
  height: 100%;
  padding: 8px;

  .admin-embedding-page__layout {
    max-width: none;
  }

  .admin-embedding-page__main {
    overflow-y: auto;
    min-height: 0;
  }

  .admin-embedding-page__card {
    flex-shrink: 0;
  }

  /* docs 탭 카드는 스크롤 가능 */
  .admin-embedding-page__main > .admin-embedding-page__card + .admin-embedding-page__card {
    flex-shrink: 1;
  }
}


/* ─── 문서 배지 스타일 (RAG 파이프라인 개선) ─── */
.admin-embedding-page {
  &__doc-info {
    display: flex;
    align-items: center;
    gap: 6px;
    flex: 1;
    min-width: 0;
  }

  &__doc-title {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &__doc-badge {
    display: inline-block;
    padding: 1px 6px;
    border-radius: 3px;
    font-size: 10px;
    line-height: 1.4;
    white-space: nowrap;
    background: #e8e8e8;
    color: #666;

    &--global {
      background: #e6f7ff;
      color: #1890ff;
      border: 1px solid #91d5ff;
    }

    &--common {
      background: #f6ffed;
      color: #52c41a;
      border: 1px solid #b7eb8f;
    }

    &--category {
      background: #fff7e6;
      color: #fa8c16;
      border: 1px solid #ffd591;
    }
  }
}
